import interfaces from '../../utils/interfaces.js';
var common = require('../../utils/common.js');
import app from "../../App.vue";
export default {
	data() {
		return {
			master_info: '',
			data:[],
			id: 0,
		}
	},
	onLoad(options) {
		this.id = options.id ? options.id : 0;
	},
	onShow() {
		this.master_info = app.globalData.master_info;
		if (this.master_info) {
			this.initData();
		} else {
			wx.reLaunch({
				url: '/pages/auth/index',
			})
		}
	},
	methods: {
		//初始化订单详情
		initData(){
			this.request({
				url: interfaces.orderPtBill,
				data: {
					id: this.id,
				},
				header: {
					token: this.master_info.token
				},
				method: 'POST',
				success: res => {
					//console.log(res);
					if (res.code == 200) {
						this.data=res.data;
					} else {
						app.isLogin(res.code, res.msg);
					}
				}
			})
		},
		del(id){
			var _this=this;
			wx.showModal({
				title: '提示',
				content: '确认删除该项？',
				success: function(res) {
					if (res.confirm) {
						_this.request({
							url: interfaces.orderPtDelOffer,
							header: {
								token: _this.master_info.token
							},
							data: {
								id: id
							},
							method: 'POST',
							success: res => {
								console.log(res);
								if (res.code == 200) {
									_this.initData();
								} else {
									app.isLogin(res.code, res.msg);
								}
							}
						})
					}
				}
			})
		}
	}
}
